.mycv__whatcv{
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

      /* ff 3.6+ */
      background:-moz-radial-gradient(circle at 3% 20%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

      /* safari 5.1+,chrome 10+ */
      background:-webkit-radial-gradient(circle at 3% 20%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* opera 11.10+ */
      background:-o-radial-gradient(circle at 3% 20%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* ie 10+ */
      background:-ms-radial-gradient(circle at 3% 20%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
      /* global 92%+ browsers support */
      background:radial-gradient(circle at 3% 20%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
  
}

.mycv__whatcv-feature{
    display: flex;
}

.mycv__whatcv-feature .mycv__features-container__feature{
    margin: 0;
}

.mycv__whatcv-feature .mycv__features-container__feature-text{
    max-width: 700px;
}

.mycv__whatcv-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.mycv__whatcv-heading h1{
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
}

.mycv__whatcv-heading p{
    font-family: var(--font-family);
font-weight: 500;
font-size: 16px;
line-height: 30px;
color: #FF8A71;
color: var(--color-subtext);
cursor: pointer;
}

.mycv__whatcv-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.mycv__whatcv-container .mycv__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.mycv__whatcv-container .mycv__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .mycv__whatcv-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .mycv__whatcv-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .mycv__whatcv-feature .mycv__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .mycv__whatcv-feature .mycv__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .mycv__whatcv-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .mycv__whatcv-container .mycv__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
} 